.App {
  text-align: center;
  font-family: Arial, sans-serif;
  margin: 20px auto;
  padding: 20px;
  width: 80%;
  max-width: 1200px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #f5f5f5;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

h1 {
  color: #33691e;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

label {
  margin-bottom: 10px;
  font-weight: bold;
}

select {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 20px;
}

button {
  padding: 10px 20px;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #388e3c;
  color: white;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #2e7d32;
}

#output {
  margin-top: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: white;
}

#output h2 {
  color: #2e7d32;
}

@media (max-width: 768px) {
  .App {
    width: 90%;
    padding: 10px;
  }

  select {
    padding: 8px;
  }

  button {
    padding: 8px 16px;
  }

  #output {
    padding: 10px;
  }
}
